import React from "react";
import MailIcon from '@mui/icons-material/Mail';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import CallIcon from '@mui/icons-material/Call';



function Contact(){
    return(
        <div className="container-fluid text-white "  id="Contact">
        <hr></hr>
           <div className="container">
           <div className="row text-center">
                <div className="col-lg-2 contact">
                  <h1 class="contact-heading">Contact</h1>
                    <p>Feel Free to reach out!</p>
                </div>
                <div className="col-lg-10 icon-div" id="icon-div">
                    <p ><CallIcon className="call"/> <span className="ml-2 contact-text">+91 9763250303</span> </p>
                    <p> <GitHubIcon /><a target="_blank" className="text-white ml-3 contact-text"  href="https://github.com/vaishnavigavhane">Github</a></p>  
                     <p><LinkedInIcon className="linkedin"/><a className="text-white ml-3 contact-text" target="_blank" href="https://www.linkedin.com/in/vaishnavi-gavhane-06027927b/">Vaishnavi Gavhane</a></p>
                   
                    <p> <MailIcon className="mail"/> <span className="ml-3 contact-text">gavhanevaishnavi135@gmail.com</span></p>
                </div>
           </div>
           </div>
        </div>
    );
};

export default Contact;